import $ from "jquery";

// export const isDev = window.location.port !== '80';

export const isDev = false;

const devBaseUrl = "https://grax-datalake-demo2.herokuapp.com";

export const baseUrl = isDev ? devBaseUrl : "";

var tokenParam = window.location.search.match(/[\?&]token\=([^&]+)/);
var orgidParam = window.location.search.match(/[\?&]orgid\=([^&]+)/);
var useridParam = window.location.search.match(/[\?&]userid\=([^&]+)/);
var sessionidParam = window.location.search.match(/[\?&]sessionid\=([^&]+)/);
var endpointParam = window.location.search.match(/[\?&]endpoint\=([^&]+)/);

export const token = tokenParam ? decodeURIComponent(tokenParam[1]) : null;
export const orgid = orgidParam ? decodeURIComponent(orgidParam[1]) : null;
export const userid = useridParam ? decodeURIComponent(useridParam[1]) : null;
export const sessionid = sessionidParam ? decodeURIComponent(sessionidParam[1]) : null;
export const endpoint = endpointParam ? decodeURIComponent(endpointParam[1]) : null;

var datalakeIndexParam = window.location.search.match(/[\?&]datalakeIndex\=([^&]+)/);
export const pageParams = {
  datalakeIndex: datalakeIndexParam ? decodeURIComponent(datalakeIndexParam[1]) : null
};

export function GetParams(params) {
  if (params === null || params === undefined) params = {};

  if (params.action === null || params.action === undefined || params.action === "")
    params.action = "load";

  params.token = token;
  params.orgid = orgid;
  params.userid = userid;
  params.sessionid = sessionid;
  params.endpoint = endpoint;

  return "?" + $.param(params);
}

export { $ };
